
.header {
  position: fixed;
  width: 100%;
  z-index: 20;
}
.header__container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: baseline;
        -ms-flex-align: baseline;
            align-items: baseline;
}
.header__logo {
    position: relative;
    top: 13px;
}
.logo__img {
  width: 131px;
  height: 51px;
}
.navigation {
  margin: 0 auto;
}
.navigation__ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.navigation__li + .navigation__li {
    margin-left: 55px;
}
.navigation__link {
    font-size: 12px;
    color: #fff;
    text-transform: uppercase;
    transition: color .3s ease-in-out;
}
.navigation__link:hover {
      color: #eab156;
}



